import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import { Wallet } from 'ethers';
import { connect } from 'react-redux';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import { Container, Row, Col } from 'reactstrap';
import { UPDATE_TOKEN_DELAY } from '../../config/appSwapConfig';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../../config/tokenBep20Config';
import { IRoutedProps, withRouter } from '../../lib/HOC';
import { TStoreState, dispatch } from '../../store/config/store';
import { TSwapSetting } from '../../store/swapSettingReducer';
import helper from '../../helper/tokenHelper';
import cryptoHelper from '../../helper/cryptoHelper';
import { DApp, ITokenBase, ITokenDetail, IPool } from '../../model/tokenModel';
import { ICurrencyInput, IRouteSwap } from '../../model/swapModel';
import {
  clearPassphrase,
  setConnectStatus,
} from '../../store/swapSettingActionCreater';
import loadingService from '../../service/loadingService';
import { getTokenInfos } from '../../service/tokenInfoService';
import { mappingModalSelectToken } from '../../service/swapMappingService';
import swapService from '../../service/swapService';
import ModalSetting from './ModalSetting/ModalSetting';
import ModalPassphrase from './ModalPassphrase/ModalPassphrase';
import ButtonWalletConnected from './ButtonWalletConnected/ButtonWalletConnected';
import ButtonSelectDApp from './ButtonSelectDApp/ButtonSelectDApp';
import ButtonSetting from './ButtonSetting/ButtonSetting';
import CurrencyInput from './CurrencyInput/CurrencyInput';
import PriceImpact from './PriceImpact/PriceImpact';
import RouteSwap from './RouteSwap/RouteSwap';
import BurnToken from './BurnToken/BurnToken';
import GasSlip from './GasSlip/GasSlip';
import ButtonSwapPosition from './ButtonSwapPosition/ButtonSwapPosition';
import ButtonBase from '../components/ButtonBase/ButtonBase';
import style from './Swap.module.scss';

interface IStoreProps {
  swapSetting: TSwapSetting;
  tokens: ITokenBase[];
}
interface IHOCProps extends IRoutedProps {}
interface IOwnProps {}
interface IOwnState {
  wallet: Wallet;
  routeSwaps: IRouteSwap[];
  dApp: DApp;
  isOpenSetting: boolean;
  isOpenPassphrase: boolean;
  input: ICurrencyInput;
  output: ICurrencyInput;
}

class Swap extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  private stopRecursion = false;
  private stableCoinAdress = [
    BUSD_BEP20.address,
    USDT_BEP20.address,
    USDC_BEP20.address,
  ];
  private tokenInput: ITokenDetail = {} as ITokenDetail;
  private tokenOutput: ITokenDetail = {} as ITokenDetail;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      wallet: {} as Wallet,
      dApp: DApp.PANCAKE,
      routeSwaps: [
        {
          symbol: BUSD_BEP20.symbol as string,
          address: BUSD_BEP20.address,
        },
        {
          symbol: BNB_BEP20.symbol as string,
          address: BNB_BEP20.address,
        },
      ],
      isOpenSetting: false,
      isOpenPassphrase: false,
      input: {
        ...(BUSD_BEP20 as ICurrencyInput),
        balance: '0',
        price: 0,
        liquidity: 0,
        quoteSymbol: '',
        value: '',
      },
      output: {
        ...(BNB_BEP20 as ICurrencyInput),
        balance: '0',
        price: 0,
        liquidity: 0,
        quoteSymbol: '',
        value: '',
      },
    };

    this.onModalSettingToggle = this.onModalSettingToggle.bind(this);
    this.onModalPassphraseToggle = this.onModalPassphraseToggle.bind(this);
    this.onModalPassphraseEnter = this.onModalPassphraseEnter.bind(this);
    this.onConnectWallet = this.onConnectWallet.bind(this);
    this.onDisconnectWallet = this.onDisconnectWallet.bind(this);
    this.onSelectDApp = this.onSelectDApp.bind(this);
    this.onCurrencyInputChange = this.onCurrencyInputChange.bind(this);
    this.onCurrencyOutputChange = this.onCurrencyOutputChange.bind(this);
    this.onBurnSettingChange = this.onBurnSettingChange.bind(this);
    this.onGasSlipSettingChange = this.onGasSlipSettingChange.bind(this);
    this.onSwapPosition = this.onSwapPosition.bind(this);
    this.onSwapToken = this.onSwapToken.bind(this);
  }

  async componentDidMount() {
    const { isConnected } = this.props.swapSetting;

    isConnected && this.initWallet();
    await this.initTokenInfos();
    this.initTokenOutput();
    await this.initSwapToken();
    this.recursionUpdateSwapToken();
  }

  componentWillUnmount() {
    this.stopRecursion = true;
  }

  private initWallet(passphrase?: string) {
    const { mnemonic: mnemonicCrypto } = this.props.swapSetting;
    const passphraseCrypto = passphrase || this.props.swapSetting.passphrase;

    if (!!mnemonicCrypto && (!!passphraseCrypto || passphraseCrypto === '')) {
      const wallet: Wallet = cryptoHelper.getWallet(
        mnemonicCrypto,
        passphraseCrypto
      );
      this.setState({ wallet }, () => {
        this.updateTokenBalance();
      });
      console.log(wallet);
    }
  }

  private async initTokenInfos() {
    loadingService.showLoading();
    await getTokenInfos();
    loadingService.hideLoading();
  }

  private initTokenOutput() {
    const { params, tokens } = this.props;

    if (params.address) {
      const tokenOutput: ITokenBase = _.find(tokens, {
        address: params.address.toLowerCase(),
      }) as ITokenBase;

      if (tokenOutput) {
        this.setState((state: IOwnState) => ({
          output: {
            ...state.output,
            ...tokenOutput,
          },
        }));
      }
    }
  }

  private async initSwapToken() {
    const { tokens } = this.props;
    const { input, output } = this.state;
    let tokenSwaps: [ITokenDetail, ITokenDetail];
    let dApp: DApp;
    let routeSwaps: IRouteSwap[];

    this.tokenInput = _.find(tokens, {
      address: input.address.toLowerCase(),
    }) as ITokenDetail;
    this.tokenOutput = _.find(tokens, {
      address: output.address.toLowerCase(),
    }) as ITokenDetail;

    tokenSwaps = await swapService.getTokenPoolSwaps([
      this.tokenInput,
      this.tokenOutput,
    ]);

    this.tokenInput = tokenSwaps[0];
    this.tokenOutput = tokenSwaps[1];
    dApp = [
      this.tokenInput.targetPool?.dApp,
      this.tokenOutput.targetPool?.dApp,
    ].includes(DApp.APE)
      ? DApp.APE
      : DApp.PANCAKE;

    this.resetTargetPoolPerDApp(dApp);

    routeSwaps = swapService.getRouteSwap(
      this.tokenInput,
      this.tokenOutput,
      dApp
    );

    this.setState(
      (state: IOwnState) => ({
        dApp,
        routeSwaps,
        input: {
          ...state.input,
          price: this.tokenInput.price as number,
          liquidity: this.tokenInput.targetPool?.liquidity as number,
          quoteSymbol: this.tokenInput.targetPool?.quoteSymbol as string,
        },
        output: {
          ...state.output,
          price: this.tokenOutput.price as number,
          liquidity: this.tokenOutput.targetPool?.liquidity as number,
          quoteSymbol: this.tokenOutput.targetPool?.quoteSymbol as string,
        },
      }),
      () => {
        this.updateTokenBalance();
      }
    );
  }

  private resetTargetPoolPerDApp(dApp: DApp) {
    let tokenInputTargetPool: IPool;
    let tokenOutputTargetPool: IPool;
    if (!_.includes(this.stableCoinAdress, this.tokenInput.address)) {
      tokenInputTargetPool = _.chain(this.tokenInput.pools)
        .filter({ dApp })
        .maxBy('liquidity')
        .value();
      this.tokenInput = {
        ...this.tokenInput,
        price: tokenInputTargetPool?.reservePrice,
        targetPool: tokenInputTargetPool,
      };
    }
    if (!_.includes(this.stableCoinAdress, this.tokenOutput.address)) {
      tokenOutputTargetPool = _.chain(this.tokenOutput.pools)
        .filter({ dApp })
        .maxBy('liquidity')
        .value();
      this.tokenOutput = {
        ...this.tokenOutput,
        price: tokenOutputTargetPool?.reservePrice,
        targetPool: tokenOutputTargetPool,
      };
    }
  }

  private async recursionUpdateSwapToken() {
    if (this.stopRecursion) {
      return;
    }

    await this.updateSwapToken();
    await new Promise((r) => setTimeout(r, UPDATE_TOKEN_DELAY));
    await this.recursionUpdateSwapToken();
  }

  private async updateSwapToken() {
    const tokenInputSnapshot: ITokenDetail = this.tokenInput;
    const tokenOutputSnapshot: ITokenDetail = this.tokenOutput;
    const dApp: DApp = this.state.dApp;
    const tokenSwaps: [ITokenDetail, ITokenDetail] =
      await swapService.getTokenSwaps(
        [tokenInputSnapshot, tokenOutputSnapshot],
        dApp
      );

    if (
      !_.includes(this.stableCoinAdress, this.tokenInput.address) &&
      _.isEqual(tokenInputSnapshot, this.tokenInput)
    ) {
      this.tokenInput = tokenSwaps[0];
      this.setState((state: IOwnState) => ({
        input: {
          ...state.input,
          price: this.tokenInput.price as number,
          liquidity: this.tokenInput.targetPool?.liquidity as number,
          quoteSymbol: this.tokenInput.targetPool?.quoteSymbol as string,
        },
      }));
    }

    if (
      !_.includes(this.stableCoinAdress, this.tokenOutput.address) &&
      _.isEqual(tokenOutputSnapshot, this.tokenOutput)
    ) {
      this.tokenOutput = tokenSwaps[1];
      this.setState((state: IOwnState) => ({
        output: {
          ...state.output,
          price: this.tokenOutput.price as number,
          liquidity: this.tokenOutput.targetPool?.liquidity as number,
          quoteSymbol: this.tokenOutput.targetPool?.quoteSymbol as string,
        },
      }));
    }
  }

  private async updateTokenBalance() {
    if (_.isEmpty(this.tokenInput) || _.isEmpty(this.tokenOutput)) {
      return;
    }

    if (_.isEmpty(this.state.wallet)) {
      this.setState((state: IOwnState) => ({
        input: {
          ...state.input,
          balance: '0',
          value: '0',
        },
        output: {
          ...state.output,
          balance: '0',
          value: '0',
        },
      }));

      return;
    }

    const tokenInputSnapshot: ITokenDetail = this.tokenInput;
    const tokenOutputSnapshot: ITokenDetail = this.tokenOutput;
    const walletAddress: string = this.state.wallet.address;
    const balances: [string, string] = await Promise.all([
      swapService.getTokenBalanceOfWallet(
        tokenInputSnapshot.address,
        walletAddress
      ),
      swapService.getTokenBalanceOfWallet(
        tokenOutputSnapshot.address,
        walletAddress
      ),
    ]);

    if (_.isEqual(tokenInputSnapshot, this.tokenInput)) {
      this.setState((state: IOwnState) => ({
        input: {
          ...state.input,
          balance: balances[0],
        },
      }));
    }

    if (_.isEqual(tokenOutputSnapshot, this.tokenOutput)) {
      this.setState((state: IOwnState) => ({
        output: {
          ...state.output,
          balance: balances[1],
        },
      }));
    }
  }

  private onConnectWallet() {
    if (!!this.props.swapSetting.mnemonic) {
      this.setState((state: IOwnState) => ({
        isOpenPassphrase: !state.isOpenPassphrase,
      }));
    } else {
      this.setState((state: IOwnState) => ({
        isOpenSetting: !state.isOpenSetting,
      }));
    }
  }

  private onDisconnectWallet() {
    this.setState({ wallet: {} as Wallet }, () => {
      this.updateTokenBalance();
    });
    dispatch(clearPassphrase());
    dispatch(setConnectStatus({ isConnected: false }));
  }

  private onModalSettingToggle() {
    this.setState((state: IOwnState) => ({
      isOpenSetting: !state.isOpenSetting,
    }));
  }

  private onModalPassphraseToggle() {
    this.setState((state: IOwnState) => ({
      isOpenPassphrase: !state.isOpenPassphrase,
    }));
  }

  private onModalPassphraseEnter(passphraseCrypto: string) {
    this.initWallet(passphraseCrypto);
    dispatch(setConnectStatus({ isConnected: true }));
  }

  private onSelectDApp(dApp: DApp) {
    this.resetTargetPoolPerDApp(dApp);

    const routeSwaps: IRouteSwap[] = swapService.getRouteSwap(
      this.tokenInput,
      this.tokenOutput,
      dApp
    );

    this.setState({ dApp, routeSwaps });
  }

  private onCurrencyInputChange(change: ICurrencyInput) {
    this.setState(
      (state: IOwnState) => ({
        input: {
          ...state.input,
          ...change,
        },
      }),
      async () => {
        const address = this.state.input.address;

        if (address !== this.tokenInput.address) {
          await this.initSwapToken();
        }
      }
    );
  }

  private onCurrencyOutputChange(change: ICurrencyInput) {
    this.setState(
      (state: IOwnState) => ({
        output: {
          ...state.output,
          ...change,
        },
      }),
      async () => {
        const address = this.state.output.address;

        if (address !== this.tokenOutput.address) {
          await this.initSwapToken();
        }
      }
    );
  }

  private onBurnSettingChange(value: { isBurnActive: boolean; burn: number }) {
    console.log('onBurnSettingChange: ', value);
  }

  private onGasSlipSettingChange(value: { gasPrice: number; slip: number }) {
    console.log('onGasSlipSettingChange: ', value);
  }

  private onSwapPosition() {
    const routeSwaps: IRouteSwap[] = swapService.getRouteSwap(
      this.tokenOutput,
      this.tokenInput,
      this.state.dApp
    );

    this.setState(
      (state: IOwnState) => ({
        routeSwaps,
        input: state.output,
        output: state.input,
      }),
      () => {
        this.tokenOutput = [
          this.tokenInput,
          (this.tokenInput = this.tokenOutput),
        ][0];
        this.updateTokenBalance();
      }
    );
  }

  private onSwapToken() {
    console.log('onSwapToken');
  }

  private buildPageTitle(): string {
    const { input, output } = this.state;
    const title = `${input.symbol}${
      !input.price ||
      this.stableCoinAdress.includes(input.address) ||
      (!!output.price &&
        ![...this.stableCoinAdress, BNB_BEP20.address].includes(output.address))
        ? ''
        : ' (' + helper.roundCurrency(input.price) + ')'
    } → ${output.symbol}${
      !output.price || this.stableCoinAdress.includes(output.address)
        ? ''
        : ' (' + helper.roundCurrency(output.price) + ')'
    }`;

    return title;
  }

  render(): ReactElement {
    const { isConnected } = this.props.swapSetting;
    const { dApp, routeSwaps, isOpenSetting, isOpenPassphrase, input, output } =
      this.state;
    const { address: addressWallet } = this.state.wallet;

    return (
      <Container fluid='lg' className='py-3 py-md-4 py-xl-5'>
        <HelmetProvider>
          <Helmet>
            <title>{this.buildPageTitle()}</title>
          </Helmet>
        </HelmetProvider>
        <Row>
          <Col
            sm={{
              offset: 2,
              size: 8,
            }}
            md={{
              offset: 3,
              size: 6,
            }}
            xl={{
              offset: 4,
              size: 4,
            }}
          >
            <div className='fk-container px-3 py-4 p-md-4'>
              <div
                className={
                  'd-flex justify-content-between align-items-center ' +
                  style.settingHeader
                }
              >
                <ButtonSelectDApp dApp={dApp} onChange={this.onSelectDApp} />
                <div className='d-flex align-items-center'>
                  {isConnected && (
                    <ButtonWalletConnected
                      addressWallet={addressWallet}
                      onClick={this.onDisconnectWallet}
                    />
                  )}
                  <div className='mx-2'></div>
                  <ButtonSetting onClick={this.onModalSettingToggle} />
                  <ModalSetting
                    isOpen={isOpenSetting}
                    onToggle={this.onModalSettingToggle}
                  />
                </div>
              </div>
              <div className='mb-4'>
                <CurrencyInput
                  symbol={input.symbol}
                  address={input.address}
                  logoURI={input.logoURI}
                  balance={input.balance}
                  price={input.price}
                  liquidity={input.liquidity}
                  quoteSymbol={input.quoteSymbol}
                  value={input.value}
                  onChange={this.onCurrencyInputChange}
                />
                <div className='mt-3 text-center'>
                  <ButtonSwapPosition onClick={this.onSwapPosition} />
                </div>
                <CurrencyInput
                  isDisable={true}
                  symbol={output.symbol}
                  address={output.address}
                  logoURI={output.logoURI}
                  balance={output.balance}
                  price={output.price}
                  value={output.value}
                  liquidity={output.liquidity}
                  quoteSymbol={output.quoteSymbol}
                  onChange={this.onCurrencyOutputChange}
                />
              </div>
              <div className='mb-5'>
                <div className='mb-3'>
                  <PriceImpact percent={5} price={0.000007} />
                </div>
                <div className='mb-3'>
                  <RouteSwap routeSwaps={routeSwaps} />
                </div>
                <div className='mb-3'>
                  <BurnToken
                    burnPredict={6}
                    onChange={this.onBurnSettingChange}
                  />
                </div>
                <div className='mb-3'>
                  <GasSlip onChange={this.onGasSlipSettingChange} />
                </div>
              </div>
              <div className='mb-2'>
                {isConnected ? (
                  <ButtonBase
                    className='fk-button-action'
                    title='Swap'
                    isDisable={false}
                    onClick={this.onSwapToken}
                  />
                ) : (
                  <ButtonBase
                    className='fk-button-action'
                    title='Connect Wallet'
                    isDisable={false}
                    onClick={this.onConnectWallet}
                  />
                )}
                <ModalPassphrase
                  isOpen={isOpenPassphrase}
                  onToggle={this.onModalPassphraseToggle}
                  onEnter={this.onModalPassphraseEnter}
                />
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    );
  }
}

const mapState = (state: TStoreState): IStoreProps => ({
  swapSetting: state.swapSetting,
  tokens: mappingModalSelectToken(state.tokens),
});

export default connect(mapState)(withRouter(Swap));
